.btn-menu {
  height: 40px;
  width: 40px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
  display: block;
  position: absolute;
  top: 10px;
  right: 20px;
}

@media screen and (min-width: 1200px) {
