    display: none;
  }
}

.btn-menu__icon--open {
  fill: #fff;
}

.btn-menu__icon--close {
  fill: #3c57a6;
  height: 60px;
  width: 60px;
}

.menu-container.is-open .btn-menu--open, .btn-menu--close {
  display: none;
}

.menu-container.is-open .btn-menu--close {
  height: 60px;
  width: 60px;
  transition: all .3s;
  display: block;
}

.menu-container.is-open .btn-menu--close:hover {
  transform: scale(1.1)rotate(180deg);
